.article {
  line-height: normal;
}

.article__title {
  display: inline-block;
  vertical-align: middle;
  margin-right: 0;
}

.article__btns {
    display: -ms-flexbox;
    display: flex;
}

.article__btn {
    background: #2b5894;
    margin-right: 20px;
}

.article__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.modal__btns {
  margin: 0 auto;
}

.modal__btn {
  margin: 20px 10px 0 10px;
}

.article-block {
  border: 1px solid #ccc;
  padding: 10px;
}



